import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import * as _ from 'lodash';
import { Observable, of } from 'rxjs';
import { filter, map, switchMap, delay } from 'rxjs/operators';

import { ENVIRONMENT } from '../../../environments/environment';

const ROOT_URL = '/API-ADMIN-DE/v1/api';

@Injectable({
  providedIn: 'root',
})
export class ApiRestSharedService {
  constructor(private http: HttpClient) {}

  getCatalogo(_nc: any): Observable<any> {
    if (ENVIRONMENT.name === 'MOCK') {
      let responseMock = {
        body: [
          {
            idItem: 1,
            nombreCatalogo: 'CAT_ADEUDO',
            claveSIM: 'CATEGORIA ADEUNO UNO',
            nombreItem: 'CATEGORIA ADEUNO UNO',
          },
          {
            idItem: 2,
            nombreCatalogo: 'CAT_ADEUDO',
            claveSIM: 'CATEGORIA ADEUDO DOS',
            nombreItem: 'CATEGORIA ADEUDO DOS',
          },
          {
            idItem: 3,
            nombreCatalogo: 'CAT_ADEUDO',
            claveSIM: 'CATEGORIA ADEUDO TRES',
            nombreItem: 'CATEGORIA ADEUDO TRES',
          },
        ],
      };
      return of(responseMock).pipe(delay(1000));
    } else {
      return this.http.get<any>(
        `${ENVIRONMENT.urlWS}${ROOT_URL}/catalogo/obtenerCatalogos`,
        { params: { nombre: _nc } }
      );
    }
  }
}
